  .page,
  .page > li {
    position: absolute;
    top: 0;
    left: 0;
    -webkit-transform-style: preserve-3d;
    transform-style: preserve-3d; 
  }
  
  .page {
    width: 100%;
    height: 98%;
    top: 1%;
    left: 3%;
    z-index: 10; 
  }
  
  .page > li {
