#about-section {
    width: 100%;
    height: 140%;
    top: 220%;
    position: absolute;
    background-image: url('../../pexels-photo-416346.jpeg');
    background-size: cover;
    background-repeat: no-repeat;
}

.about-text-area {
    margin: 120px 0 0 150px;
    width: 70%;
}

.about-text-area h1 {
    font-family: 'Dancing Script', cursive, 'Comfortaa', 'Trebuchet MS', 'Segoe UI', 'Calibri', 'Lucida Grande', sans-serif;
    font-size: 50px;
    margin: 50px 0 20px 0;
    width: 100%;
    position: relative;
    -webkit-transform: rotate(-2deg);
        -ms-transform: rotate(-2deg);
            transform: rotate(-2deg);
}

.about-text-area h2 {
    font-family: 'Pacifico', cursive, 'Comfortaa', 'Trebuchet MS', 'Segoe UI', 'Calibri', 'Lucida Grande', sans-serif;
    text-align: left;
    margin-top: 0;
    text-decoration: underline;
    -webkit-transform: rotate(-1.2deg);
        -ms-transform: rotate(-1.2deg);
            transform: rotate(-1.2deg);
}

.about-text-area p {
    font-family: 'Comfortaa', 'Trebuchet MS', 'Segoe UI', 'Calibri', 'Lucida Grande', sans-serif;
    display: inline-block;
    margin: 5px 0px 50px 0px;
    width: 80%;
    text-align: left;
    -webkit-transform: rotate(-0.8deg);
        -ms-transform: rotate(-0.8deg);
            transform: rotate(-0.8deg);
    /* background: lightblue; */
}



