.arrow-down-container {
    position: absolute;
    bottom: 50px;
    left: 50%;
    background: #26419A;
    opacity: 0.4;
    width: 70px;
    height: 70px;
    border-radius: 35px;
    -webkit-transition: 0.3s;
    -o-transition: 0.3s;
    transition: 0.3s;
}

.arrow-down {
    color: white;
    font-size: 35px;
    margin-top: 18px;
    cursor: pointer;
}

.arrow-down-container:hover {
    opacity: 0.6;
}

@media (max-width: 700px) {
    .arrow-down-container {
        display: none;
    }
}



