.darky {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: black;
    opacity: 0;
    -webkit-transition: 1s;
    -o-transition: 1s;
    transition: 1s;
}

.trending-container {
    background-image: url('../../table-top.jpeg');
    background-size: cover;
    background-repeat: no-repeat;
    position: absolute;
    top: 100%;
    width: 100%;
    height: 120%;
    -webkit-transition: 1s;
    -o-transition: 1s;
    transition: 1s;
    overflow: hidden;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}

.trending-books-list {
    list-style: none;
    position: absolute;
    bottom: 200px;
    left: 0;
    width: 100%;
    height: 350px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    padding: 0;
    /* background: red; */
}

.book-container {
    width: 410px;
    min-height: 300px;
    display: inline-block;
    padding: 0 0 10px 157px;
    margin: 20px 5px;
    vertical-align: top;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    /* background: lightblue; */
    z-index: 0;
}

@media (max-width: 1260px) and (min-width: 900px) {
    .trending-books-list {
        height: 600px;
        bottom: 0px;
    }

    #book1 {
        position: absolute;
        left: 10px;
    }

    #book2 {
        position: absolute;
        bottom: 0;
        left: 240px;
    }

    #book3 {
        position: absolute;
        left: 470px;
    }
}

@media (max-width: 900px) {
    .trending-books-list {
        bottom: 120px;
    }

    #book2,
    #book3 {
        display: none;
    }

    #book1 {
        position: absolute;
        bottom: 0px;
        left: 50px;
    }
}


