.intro-page {
    position: absolute;
    background-image: url('../../pexels-photo-210661.jpeg');
    background-size: cover;
    background-repeat: no-repeat;
    background-attachment: fixed;
    width: 100%;
    height: 100%;
}

.palate {
    position: absolute;
    width: 400px;
    height: 400px;
    right: 80px;
    top: 150px;
    background: rgba(255, 255, 255, 0.5);
    overflow: hidden;
    border-radius: 200px;
    /* box-shadow: 2px 3px 6px 0px #999; */
}

h2 {
    font-family: 'Ubuntu', sans-serif;
    font-weight: normal;
    font-style: italic;
    font-size: 35px;
    margin: 130px 60px 0px 60px;
    width: 70%;
}

p#about-link {
    cursor: pointer;
    text-decoration: underline;
    color: #26419A;
    font-family: 'Ubuntu', sans-serif;
    font-style: italic;
    display: inline-block;
    margin-top: 20px;
    font-size: 18px;
}

@media (max-width: 700px) {
    .palate {
        background: none;
        width: 100%;
        right: 0;
        color: white;
    }

    h2 {
        margin-top: 40px;
    }
}



