.Header {
    font-family: 'Quicksand', sans-serif;
    position: fixed;
    left: 0;
    top: 0;
    height: 100px;
    width: 100%;
    /* min-width: 700px; */
    background-color: rgba(0, 0, 0, 0.3);
    color: #f0f0f0;
    overflow: hidden;
    z-index: 100;
    -webkit-transition: 0.3s;
    -o-transition: 0.3s;
    transition: 0.3s;
}

h1 {
    display: inline-block;
    position: absolute;
    /* width: 70%; */
    text-align: center;
    left: 0;
    min-width: 150px;
    margin-left: 20px;
    margin-top: 25px;
    cursor: default;
    font-size: 2.2em;
}

.Links {
    width: 200px;
    position: absolute;
    right: 0;
    font-size: 1.3em;
    font-weight: bold;
    margin: 20px 20px;
}

button {
    position: absolute;
    width: 150px;
    height: 60px;
    background: none;
    border: 1px solid gray;
    border-radius: 4px;
    -webkit-transition: .6s;
    -o-transition: .6s;
    transition: .6s;
    color: white;
    font-family: 'Quicksand', sans-serif;
    font-size: 1em;
    margin-left: 20px;
    overflow: hidden;
}

button[name='SignUp'] {
    right: 200px;
}

button[name='SignIn'] {
    right: 20px;
}

button:focus {
    outline: none;
}

button::before {
    content: '';
    display: block;
    position: absolute;
    width: 37.5px;
    height: 100%;
    left: 0;
    top: 0;
    -webkit-transform: translateX(-80px) skewX(-15deg);
        -ms-transform: translateX(-80px) skewX(-15deg);
            transform: translateX(-80px) skewX(-15deg);
    -webkit-filter: blur(30px);
            filter: blur(30px);
    background: rgba(255, 255, 255, 0.5);
    opacity: .5s;
}

button::after {
    content: '';
    display: block;
    position: absolute;
    left: 30px;
    top: 0;
    width: 30px;
    height: 100%;
    -webkit-transform: translate(-55px) scaleX(-15deg);
        -ms-transform: translate(-55px) scaleX(-15deg);
            transform: translate(-55px) scaleX(-15deg);
    -webkit-filter: blur(30px);
            filter: blur(30px);
    background: rgba(255, 255, 255, 0.2);
    opacity: 0;
}

button:hover {
    background: #5B5E6A;
    cursor: pointer;
}

button:hover::before {
    -webkit-transform: translateX(187.5px) skewX(-15deg);
        -ms-transform: translateX(187.5px) skewX(-15deg);
            transform: translateX(187.5px) skewX(-15deg);
    opacity: .6s;
    -webkit-transition: .7s;
    -o-transition: .7s;
    transition: .7s;
}

button:hover::after {
    -webkit-transform: translateX(187.5px) skewX(-15deg);
        -ms-transform: translateX(187.5px) skewX(-15deg);
            transform: translateX(187.5px) skewX(-15deg);
    opacity: 1;
    -webkit-transition: .7s;
    -o-transition: .7s;
    transition: .7s
}

@media (max-width: 700px) {
    #signup-button {
        display: none;
    }

    #signin-button {
        background: #5B5E6A;
        height: 40px;
        width: 100px;
        border-radius: 20px;
        font-size: 16px;
        top: 10px;
    }

    #signin-button::after,
    #signin-button::before {
        -webkit-transition: 0s;
        -o-transition: 0s;
        transition: 0s;
    }
}



